import React, { useEffect, useState } from 'react'
import './style.css'
import * as dultlogo from '../../assets/Clean Walk/Loading_Screen/LD_DULT_Logo.png'

import { useHistory, useLocation, useParams } from 'react-router-dom';

import { useColourObjectStore } from '../../store/ColourStore'
import { logostore } from '../../store/logoStore';
import { logopositionstore } from '../../store/logopositionStore';
import { instancestore } from '../../store/InstanceStore';
import { useActiveScreen } from '../../store/ActiveScreen';
import { userActiveLevelDetailsStore } from '../../store/ActiveLevelDetailsStore'

import { useAuth } from '../../store/authStore'


const Splash = (props: any) => {

  const { instance, loadinstancewithcallback } = instancestore();
  const { logo, loadlogo, loadlogowithcallback, loadlogowithid } = logostore();
  const { logoposition, loadlogo_arrayposition_iid, loadlogo_arrayposition } = logopositionstore();
  const { setinstancecolor, colourObj, setcolourObj, loaddefaultcolor } = useColourObjectStore();
  const { loadactiveInstancebyID, } = instancestore();
  const { setActiveScreen } = useActiveScreen();
  const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();
  // const [sTimeout, setStimeout] = useState(props.previewScreen ? 100000 : 3000);
  const [logoArray, setLogoArray] = useState([]);


  const history = useHistory();
  let params = useParams();
  const { auth } = useAuth();
  const search = useLocation().search;


  useEffect(() => {
    console.log("props.previewScreen", props.previewScreen);

    if (props.previewScreen === undefined) {
      if (!instance) {
        loadinstancewithcallback(callback)
      } else {
        callback(logo)
      }
    }

    if (!colourObj) {
      loaddefaultcolor()
    }

  }, [])

  const callback = (list: any) => {
    list.map((obj: any) => {
      if ((String(params.instanceId)) === (String(obj.i_title)?.replace(" ", "").toLowerCase())) {

        localStorage.setItem('TempActiveInstanceInfo', JSON.stringify(obj))
        validinstancetitle(obj)
      } else if (localStorage.getItem('TempActiveInstanceInfo') !== null) {
        let tempactiveinstance = JSON.parse(String(localStorage.getItem('TempActiveInstanceInfo')))
        if (auth) { history.replace("/" + String((tempactiveinstance.i_title).replace(/\s/g, "").toLowerCase())) }
        validinstancetitle(tempactiveinstance)
      } else {
        history.replace("/")
        if (!logoposition) { loadlogo_arrayposition_iid(37) }
        setTimeout(() => {
          setActiveScreen("StartScreen")
        }, 3000);
      }
    })
  }

  const validinstancetitle = (element: any) => {
    setinstancecolor(element.color_field)
    loadlogowithid(element.id)
    if (!logoposition) { loadlogo_arrayposition_iid(element.id) }
    if (auth) {
      loadactiveInstancebyID(element.id)
      setTimeout(() => {
        const id = new URLSearchParams(search).get('id');
        if (id) {
          loadActiveleveldetailsbyqr(id)
          setActiveScreen("Level")
        }
        else {
          setActiveScreen("Home")
        }
      }, 3000);
    } else {
      setTimeout(() => {
        setActiveScreen("StartScreen")
      }, 3000);
    }
  }


  const logoimage_position = ((obj: any) => {
    const selectcase = (number: any) => {
      if (number === 1)
        return "logostyle1";
      else
        if (number > 1 && number < 4)
          return "logostyle2";
        else
          if (number >= 4 && number <= 6)
            return "logostyle3";
          else
            if (number > 6)
              return "logostyle4";
    }
    let a = selectcase(obj.length)
    return obj.map((obj1: any) => {
      return <img className={a} src={obj1.i_logo} />
    })
  })

  const newLogoimage_position = ((obj: any) => {
    console.log("objobjobj", obj.length);
    const selectcase = (number: any) => {
      if (number === 1)
        return "logostyle1";
      else
        if (number > 1 && number < 4)
          return "logostyle2";
        else
          if (number >= 4 && number <= 6)
            return "logostyle3";
          else
            if (number > 6)
              return "logostyle4";
    }
    let a = selectcase(obj.length)
    console.log("aaaaa", a);

    return obj.map((obj1: any) => {
      console.log("obj1obj1", obj1);

      return <img className={a} src={obj1.i_logo} />
    })
  })

  return (
    <div className="logos_section">

      <div className="row1">
        <div className="loadinnerrow">
          <img className="logostyle1" src={dultlogo} />
        </div>
      </div>

      {!props.previewScreen ?
        (logoposition?.section2?.length > 0) &&
        <div className='row2'>
          <div className="innerrowtitle"><u>PARTNERS</u></div>
          <div className="loadinnerrow">
            {(logoposition) && logoposition?.section2?.map(logoimage_position)}
          </div>
        </div>

        :

        <div className='row2'>
          <div className="innerrowtitle"><u>PARTNERS</u></div>
          <div className="loadinnerrow">
            {newLogoimage_position(logo)}
          </div>
        </div>
      }

    </div>
  )
}

export default Splash
